@import url("https://fonts.googleapis.com/css2?family=Figtree:ital,wght@0,300..900;1,300..900&display=swap");

:root {
	// Typography
	--font-family: "Figtree", sans-serif;
	// Colors
	--color-dark: #030027;
	--color-primary: #09bc8a;
	--color-98w: #f9f9f9;
	--color-94w: #f0f0f0;
	--color-86w: #dbdbdb;
	--color-70w: #b3b3b3;
	--color-38w: #616161;
}

// Breakpoints
$breakpoints: (
	"sm": "640px",
	"md": "768px",
	"lg": "1024px",
	"xl": "1280px",
	"2xl": "1536px",
);

// Z-indexes
$z-base: 100;
$z-above: 1;
$z-below: -1;

$z-header: $z-base;
$z-footer: $z-base;
$z-nav: $z-header + $z-above;
$z-modal: $z-nav + $z-above;

$z-page-dimmer: $z-base - $z-below;
$z-overlay: $z-base + $z-above;

// Transitions
$duration-nano: 100ms;
$duration-sm: 150ms;
$duration: 250ms;
$duration-lg: 350ms;
$duration-xl: 500ms;
