.clients {
	$block: &;
	max-width: 100%;
	background-color: rgba(3, 0, 39, 0.03);
	padding-top: 64px;
	padding-bottom: 64px;
	&[data-animated="true"] {
		@apply overflow-hidden;
		// mask: linear-gradient(90deg, transparent, white 5%, white 95%, transparent);
		#{$block}__inner {
			width: max-content;
			@apply flex-nowrap;
			animation: scroll 30s linear infinite;
		}
	}
	&__inner {
		@include flex(center, center);
		@apply space-x-64;
		// @apply space-x-14;
	}
}

@keyframes scroll {
	to {
		transform: translate(calc(-50% - 128px));
	}
}
