.content-hero {
	@apply mb-36;
	@include grid;
	margin-top: calc(144px + 108px);
	&__inner {
		@apply col-start-2 col-span-8;
	}
	&__super {
		font-size: toRem(24);
		@apply font-semibold mb-4;
	}
	&__title {
		font-size: toRem(120);
		@apply font-semibold mb-6;
	}
	&__description {
		font-size: toRem(28);
		line-height: 1.5;
	}
}
