// To Rem
@mixin toRem($property, $value) {
	#{$property}: calc($value / 16) + rem;
}

// as function
@function toRem($value) {
	$remValue: calc($value / 16) + rem;
	@return $remValue;
}

// Containers
@each $name, $size in $breakpoints {
	.container-#{$name} {
		@apply max-w-screen-#{$name} mx-auto;
	}
}

// Transitions
@mixin transition($prop: all, $dur: $duration-sm, $timing: ease-in-out) {
	transition-property: $prop;
	transition-duration: $dur;
	transition-timing-function: $timing;
}

//// Typography
@mixin typography($size, $height: 1.2) {
	font-size: toRem($size);
	line-height: $height;
}

//// Positioning
// Flex
@mixin flex($align: null, $justify: null, $direction: null) {
	display: flex;
	@if $align {
		align-items: $align;
	}

	@if $justify {
		justify-content: $justify;
	}

	@if $direction {
		flex-direction: $direction;
	}
}

@mixin grid($grid: 12) {
	@apply grid gap-6;
	grid-template-columns: repeat($grid, minmax(0, 1fr));
}

@mixin pseudo($display: block, $pos: absolute, $content: "") {
	content: $content;
	display: $display;
	position: $pos;
}

@mixin breakpoint-up($breakpoint) {
	@media (min-width: map-get($breakpoints, $breakpoint)) {
		@content;
	}
}

@mixin breakpoint-down($breakpoint) {
	@media (max-width: map-get($breakpoints, $breakpoint)) {
		@content;
	}
}
