.header {
	@apply px-16 py-6 fixed;
	@include flex(center, space-between, row);
	top: 0;
	width: 100%;
	z-index: $z-header;
	background-color: var(--color-98w);
	&__logo {
		height: 60px;
		img {
			height: 100%;
		}
	}
	&__menu {
		@apply flex flex-row gap-14;
		&__item {
			font-size: toRem(20);
			@apply font-semibold;
		}
	}
}
