// General

// Include fluid type and smooth scrolling
$min_width: 320px;
$max_width: 1900px;
$min_font: 8px;
$max_font: 16px;

html {
	@include fluid-type($min_width, $max_width, $min_font, $max_font);
	scroll-behavior: smooth;
}

.page {
	background-color: var(--color-98w);
}
