.footer {
	@apply pb-12 pt-24;
	background-color: var(--color-dark);
	color: var(--color-98w);
	a {
		color: var(--color-98w);
	}
	&__inner {
	}
	&__upper {
		@apply mb-60 font-semibold text-5xl;
		@include grid;
	}
	&__cta {
		@apply col-span-6 space-y-3;
		max-width: 16ch;
		line-height: 1.2;
		a {
			color: var(--color-primary);
		}
	}
	&__menu {
		@apply grid grid-cols-2 gap-6 col-start-7 col-span-6;
	}
	&__lower {
		@include flex(center, space-between);
		span {
			@apply mr-20;
		}
		nav {
			@apply space-x-20;
		}
	}
}
