.work {
	@apply space-y-6;

	&__image {
		overflow: hidden;
		.no-touch &:hover {
			img {
				transform: scale(1);
			}
		}
		img {
			@include transition;
			max-height: 650px;
			min-height: 100%;
			min-width: 100%;
			max-width: 100%;
			object-fit: cover;
			transform: scale(1.08);
		}
	}
	&__title {
		@apply font-medium text-2xl;
		a {
			@apply flex space-x-4;
		}
	}
}
