.button {
	$block: &;
	@include transition;
	@apply inline-block;
	&__inner {
		@include flex(center);
		@apply space-x-4;
	}
	&__icon {
		@include transition($dur: $duration-nano);
	}
	&--style {
		&--primary {
			@apply px-16 py-7 bg-dark text-98white;
			.no-touch &:hover {
				@apply bg-slate-800;
			}
		}
		&--secondary {
			.no-touch &:hover {
				#{$block}__icon {
					@apply translate-x-1;
				}
			}
		}
	}
	&--size {
		&--regular {
			@apply text-xl;
			#{$block}__icon {
				@apply text-2xl;
			}
		}
		&--large {
			@apply text-3xl;
			#{$block}__icon {
				@apply text-3xl;
			}
		}
	}
}
