.hero {
	@include grid;
	@apply space-y-9 mb-64;
	margin-top: calc(144px + 108px); // margin + navbar height
	&__title {
		@apply col-span-10;
		&__display {
			font-size: 7.5rem;
			line-height: 1.2;
		}
	}
	&__buttons {
		@apply col-span-12;
		@include flex(center, null, row);
		@apply space-x-16 font-semibold;
	}
}
