.service-block {
	@apply mb-28;
	&__title {
	}
	&__demo {
		@apply mt-12;
		@include grid;
		.work {
			&-5 {
				@apply col-span-5;
			}
			&-7 {
				@apply col-span-7;
			}
			&__image {
				img {
					max-height: 423px;
				}
			}
		}
	}
	&__content {
		@include grid;
		&__inner {
			@apply col-start-2 col-span-10 mt-28;
			@include grid(10);
		}
		&__description {
			@apply col-span-5;
			@include typography(24, 1.5);
		}
		&__list {
			@apply col-span-4 col-start-7 space-y-8;
			@include typography(20, 1);
			columns: 2;
		}
	}
}
