@import url("https://fonts.googleapis.com/css2?family=Figtree:ital,wght@0,300..900;1,300..900&display=swap");
:root {
  --font-family: "Figtree", sans-serif;
  --color-dark: #030027;
  --color-primary: #09bc8a;
  --color-98w: #f9f9f9;
  --color-94w: #f0f0f0;
  --color-86w: #dbdbdb;
  --color-70w: #b3b3b3;
  --color-38w: #616161;
}

.container-sm {
  @apply max-w-screen-sm mx-auto;
}

.container-md {
  @apply max-w-screen-md mx-auto;
}

.container-lg {
  @apply max-w-screen-lg mx-auto;
}

.container-xl {
  @apply max-w-screen-xl mx-auto;
}

.container-2xl {
  @apply max-w-screen-2xl mx-auto;
}

@tailwind base;
@tailwind components;
@tailwind utilities;
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
}

::selection {
  color: var(--color-98w);
  background-color: var(--color-dark);
}
.footer ::selection {
  color: var(--color-dark);
  background-color: var(--color-86w);
}

body,
input,
select,
textarea {
  font-family: var(--font-family);
  line-height: 1.2;
}

a {
  color: var(--color-dark);
  text-decoration: none;
}

p:not(:last-child) {
  margin-bottom: 1em;
}

.display {
  font-size: 7.5rem;
  @apply font-semibold;
}

h1,
.h1 {
  font-size: 3rem;
}

h2,
.h2 {
  font-size: 2rem;
}

h3,
.h3 {
  font-size: 1.75rem;
}

h4,
.h4 {
  font-size: 1.5rem;
}

h5,
.h5 {
  font-size: 1.25rem;
}

/* Hover Slide*/
.hover-slide {
  --thick: 6px;
  --dist: -12px;
  @apply inline-block relative;
}
.hover-slide--nano {
  --thick: 3px;
  --dist: -8px;
}
.hover-slide::before, .hover-slide::after {
  content: "";
  display: block;
  position: absolute;
  height: var(--thick);
  bottom: var(--dist);
  width: 0;
}
.hover-slide::before {
  left: 0;
  transition: width 0s ease, background 350ms ease;
}
.hover-slide::after {
  right: 0;
  background: currentColor;
  transition: width 350ms ease;
}
.no-touch .hover-slide:hover:before {
  width: 100%;
  background: currentColor;
  transition: width 350ms ease;
}
.no-touch .hover-slide:hover:after {
  width: 100%;
  background: transparent;
  transition: all 0s ease;
}

.article {
  @apply grid gap-6;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  @apply space-y-12;
}
.article > * {
  @apply col-span-8;
}
.article__content {
  font-size: 1.5rem;
  line-height: 1.2;
  @apply font-medium;
}
.button {
  transition-property: all;
  transition-duration: 150ms;
  transition-timing-function: ease-in-out;
  @apply inline-block;
}
.button__inner {
  display: flex;
  align-items: center;
  @apply space-x-4;
}
.button__icon {
  transition-property: all;
  transition-duration: 100ms;
  transition-timing-function: ease-in-out;
}
.button--style--primary {
  @apply px-16 py-7 bg-dark text-98white;
}
.no-touch .button--style--primary:hover {
  @apply bg-slate-800;
}
.no-touch .button--style--secondary:hover .button__icon {
  @apply translate-x-1;
}
.button--size--regular {
  @apply text-xl;
}
.button--size--regular .button__icon {
  @apply text-2xl;
}
.button--size--large {
  @apply text-3xl;
}
.button--size--large .button__icon {
  @apply text-3xl;
}

.clients {
  max-width: 100%;
  background-color: rgba(3, 0, 39, 0.03);
  padding-top: 64px;
  padding-bottom: 64px;
}
.clients[data-animated=true] {
  @apply overflow-hidden;
}
.clients[data-animated=true] .clients__inner {
  width: max-content;
  @apply flex-nowrap;
  animation: scroll 30s linear infinite;
}
.clients__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  @apply space-x-64;
}

@keyframes scroll {
  to {
    transform: translate(calc(-50% - 128px));
  }
}
.content-hero {
  @apply mb-36;
  @apply grid gap-6;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  margin-top: 252px;
}
.content-hero__inner {
  @apply col-start-2 col-span-8;
}
.content-hero__super {
  font-size: 1.5rem;
  @apply font-semibold mb-4;
}
.content-hero__title {
  font-size: 7.5rem;
  @apply font-semibold mb-6;
}
.content-hero__description {
  font-size: 1.75rem;
  line-height: 1.5;
}

.display-text {
  @apply grid gap-6;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  @apply py-24;
}
.display-text div {
  @apply col-start-2 col-span-9;
  font-size: 3.5rem;
  line-height: 1.2;
  font-family: "Century";
}

.footer {
  @apply pb-12 pt-24;
  background-color: var(--color-dark);
  color: var(--color-98w);
}
.footer a {
  color: var(--color-98w);
}
.footer__upper {
  @apply mb-60 font-semibold text-5xl;
  @apply grid gap-6;
  grid-template-columns: repeat(12, minmax(0, 1fr));
}
.footer__cta {
  @apply col-span-6 space-y-3;
  max-width: 16ch;
  line-height: 1.2;
}
.footer__cta a {
  color: var(--color-primary);
}
.footer__menu {
  @apply grid grid-cols-2 gap-6 col-start-7 col-span-6;
}
.footer__lower {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__lower span {
  @apply mr-20;
}
.footer__lower nav {
  @apply space-x-20;
}

.header {
  @apply px-16 py-6 fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  top: 0;
  width: 100%;
  z-index: 100;
  background-color: var(--color-98w);
}
.header__logo {
  height: 60px;
}
.header__logo img {
  height: 100%;
}
.header__menu {
  @apply flex flex-row gap-14;
}
.header__menu__item {
  font-size: 1.25rem;
  @apply font-semibold;
}

.hero {
  @apply grid gap-6;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  @apply space-y-9 mb-64;
  margin-top: 252px;
}
.hero__title {
  @apply col-span-10;
}
.hero__title__display {
  font-size: 7.5rem;
  line-height: 1.2;
}
.hero__buttons {
  @apply col-span-12;
  display: flex;
  align-items: center;
  flex-direction: row;
  @apply space-x-16 font-semibold;
}

.icon {
  display: flex;
  align-items: center;
}

.js-on-screen {
  @apply opacity-0 relative;
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}
.js-on-screen.from-top {
  transform: translateY(-50px) translateZ(0);
}
.js-on-screen.from-right {
  transform: translateX(50px) translateZ(0);
}
.js-on-screen.from-bottom {
  transform: translateY(50px) translateZ(0);
}
.js-on-screen.from-left {
  transform: translateX(-50px) translateZ(0);
}
.js-on-screen.is-on-screen {
  opacity: 1;
  transform: none;
}

.overlay {
  @apply relative flex items-center justify-center p-4;
  z-index: 101;
}
.overlay__content {
  @apply bg-white w-full relative shadow;
  animation: showOverlayContent 0.4s forwards;
}

@keyframes showOverlayContent {
  from {
    opacity: 0;
    transform: translateY(5px);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
@keyframes hideOverlayContent {
  from {
    opacity: 1;
    transform: none;
  }
  to {
    opacity: 0;
    transform: translateY(5px);
  }
}
.page {
  @apply overflow-hidden;
}
.page::after {
  @apply fixed inset-0 opacity-0 invisible pointer-events-auto;
  content: "";
  z-index: 101;
}
.is-dimmed .page::after {
  @apply visible pointer-events-auto opacity-100;
}
.is-locked .page {
  @apply fixed left-0 w-full;
}

.service-block {
  @apply mb-28;
}
.service-block__demo {
  @apply mt-12;
  @apply grid gap-6;
  grid-template-columns: repeat(12, minmax(0, 1fr));
}
.service-block__demo .work-5 {
  @apply col-span-5;
}
.service-block__demo .work-7 {
  @apply col-span-7;
}
.service-block__demo .work__image img {
  max-height: 423px;
}
.service-block__content {
  @apply grid gap-6;
  grid-template-columns: repeat(12, minmax(0, 1fr));
}
.service-block__content__inner {
  @apply col-start-2 col-span-10 mt-28;
  @apply grid gap-6;
  grid-template-columns: repeat(10, minmax(0, 1fr));
}
.service-block__content__description {
  @apply col-span-5;
  font-size: 1.5rem;
  line-height: 1.5;
}
.service-block__content__list {
  @apply col-span-4 col-start-7 space-y-8;
  font-size: 1.25rem;
  line-height: 1;
  columns: 2;
}

.work-display {
  @apply grid gap-6;
  grid-template-columns: repeat(12, minmax(0, 1fr));
}
.work-display .work-full {
  @apply col-start-2 col-span-10;
}
.work-display .work-4 {
  @apply col-start-2 col-span-4;
}
.work-display .work-5 {
  @apply col-start-7 col-span-5;
}

.work-display-cta {
  font-size: 2rem;
}

.work {
  @apply space-y-6;
}
.work__image {
  overflow: hidden;
}
.no-touch .work__image:hover img {
  transform: scale(1);
}
.work__image img {
  transition-property: all;
  transition-duration: 150ms;
  transition-timing-function: ease-in-out;
  max-height: 650px;
  min-height: 100%;
  min-width: 100%;
  max-width: 100%;
  object-fit: cover;
  transform: scale(1.08);
}
.work__title {
  @apply font-medium text-2xl;
}
.work__title a {
  @apply flex space-x-4;
}

html {
  scroll-behavior: smooth;
}
html {
  font-size: 8px;
}
@media screen and (min-width: 320px) {
  html {
    font-size: calc(8px + 8 * (100vw - 320px) / 1580);
  }
}
@media screen and (min-width: 1900px) {
  html {
    font-size: 16px;
  }
}

.page {
  background-color: var(--color-98w);
}