body,
input,
select,
textarea {
	font-family: var(--font-family);
	line-height: 1.2;
}

a {
	color: var(--color-dark);
	text-decoration: none;
}

p {
	&:not(:last-child) {
		margin-bottom: 1em;
	}
}

.display {
	font-size: toRem(120);
	@apply font-semibold;
}

h1,
.h1 {
	font-size: toRem(48);
}
h2,
.h2 {
	font-size: toRem(32);
}
h3,
.h3 {
	font-size: toRem(28);
}
h4,
.h4 {
	font-size: toRem(24);
}
h5,
.h5 {
	font-size: toRem(20);
}

/* Hover Slide*/
.hover-slide {
	--thick: 6px;
	--dist: -12px;
	&--nano {
		--thick: 3px;
		--dist: -8px;
	}
	@apply inline-block relative;
	// padding-bottom: 1rem;
	&::before,
	&::after {
		@include pseudo();
		height: var(--thick);
		bottom: var(--dist);
		width: 0;
	}
	&::before {
		left: 0;
		transition: width 0s ease, background $duration-lg ease;
	}
	&::after {
		right: 0;
		background: currentColor;
		transition: width $duration-lg ease;
	}
	.no-touch &:hover:before {
		width: 100%;
		background: currentColor;
		transition: width $duration-lg ease;
	}
	.no-touch &:hover:after {
		width: 100%;
		background: transparent;
		transition: all 0s ease;
	}
}
